  // Treble practice
  function addTrebleAuto(
    value: number,
    ring?: "SINGLE" | "DOUBLE" | "TRIPLE" | "BULL" | "INNER_BULL",
    sector?: number | null,
  ): boolean {
    if (trebleFinished) return false;
    recordDart("Treble Practice");
    const isHit =
      (ring === "TRIPLE" && sector === trebleTarget) ||
      (!ring && value === trebleTarget * 3);
    if (isHit) setTrebleHits((h) => h + 1);
    setTrebleDarts((d) => {
      const nd = d + 1;
      // Auto-rotate target every 3 darts among T20â†’T19â†’T18
      if (nd % 3 === 0 && [20, 19, 18].includes(trebleTarget)) {
        const cycle = ["T20", "T19", "T18"];
        const idx = cycle.indexOf(`T${trebleTarget}`);
        const next = cycle[(idx + 1) % cycle.length];
        setTrebleTarget(Number(next.replace("T", "")));
      }
      // End session at configured throws
      if (trebleMaxDarts > 0 && nd >= trebleMaxDarts) setTrebleFinished(true);
      return nd;
    });
    return true;
  }
  function resetTreble() {
    setTrebleHits(0);
    setTrebleDarts(0);
    setTrebleFinished(false);
    resetDartCount("Treble Practice");
  }

